.footer {
  display: flex;
  align-items: center;
  min-height: 100px;
  margin-top: 100px;
  padding: 20px 40px;

  @include vp--1023 {
    padding: 10px 24px;
  }

  @include vp--767 {
    padding: 10px 16px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include vp--1023 {
      justify-content: center;
      flex-direction: column;
    }
  }

  &__logo {
    width: 100px;
    height: 80px;

    @include vp--1023 {
      width: 60px;
      height: 60px;
    }

    svg {
      object-fit: contain;
      width: 100px;
      height: 80px;

      @include vp--1023 {
        width: 60px;
        height: 60px;
      }
    }
  }

  &__box-text {
    display: flex;
    flex-direction: column;

    @include vp--1023 {
      margin-top: 20px;
    }
  }

  &__text {
    margin-bottom: 20px;
    text-align: center;

    p {
      margin: 0;
      padding: 0;

      font-size: 12px;
      line-height: 20px;
    }
  }

  &__link {
    display: flex;
    flex-direction: column;
    text-align: center;

    a {
      font-size: 12px;
      line-height: 20px;
      text-decoration: underline;
      opacity: 1;
      transition: opacity $trans--300;

      &:hover,
      &:focus,
      &:active {
        opacity: 0.6;
      }
    }
  }

  &__socials {
    @include vp--1023 {
      margin-top: 20px;
    }

    a {
      opacity: 1;
      transition: opacity $trans--300;

      &:hover,
      &:focus,
      &:active {
        opacity: 0.6;
      }
    }

    svg {
      width: 40px;
      height: 40px;
    }
  }
}
