.account {
  @include vp--500 {
    margin: 20px;
    padding-top: 20px;
  }

  h1 {
    margin-bottom: 60px;
  }

  .input {
    display: flex;
    flex-direction: column;
  }

  &__button {
    max-width: 300px;
    margin-bottom: 20px;

    @include vp--500 {
      max-width: 100%;
    }
  }

  &__conf {
    max-width: 300px;
    font-size: 12px;
    line-height: 20px;

    @include vp--500 {
      max-width: 100%;
    }
  }
}
