.card-video-set {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 60px;
  padding: 20px;
  background-color: $color-grey;

  @include vp--1023 {
    margin-bottom: 20px;
  }

  p {
    margin-bottom: 10px;
    color: $color-default-black;
    font-size: 12px;
    font-weight: bold;
  }

  video {
    height: 200px;
    margin-right: 20px;
    width: 400px;

    @include vp--1023 {
      width: 100%;
    }

    object-fit: cover;
    cursor: pointer;
  }

  span {
    margin-left: 10px;
    font-weight: 400;
  }
}
